        "Der Schweizer Aktienmarkt kam zum Wochenauftakt nicht vom Fleck und schloss praktisch unverndert. Im Vorfeld der mit Spannung erwarteten Semester-Berichterstattung, die ab Mitte Monat richtig an Fahrt aufnehmen wird, hielten sich die Neuigkeiten aus dem Unternehmensbereich in Grenzen. Auch die Impulse aus bersee fehlten, weil die US-Aktienmrkte wegen eines Feiertages geschlossen blieben.",
