      body_in_english:
        "Stock markets US stock markets Dow Jones: closed S & P500: closed Nasdaq: closed European stock markets EuroStoxx50: + 0.07% DAX: + 0.08% SMI: + 0.02% Asian markets Nikkei 225: + 0.26% HangSeng: -0.62% S & P / ASX 200: - 0.62% The Swiss stock market did not move at the beginning of the week. In the run-up to the eagerly awaited semester reporting, which will really pick up speed from the middle of the month, the news from the corporate division was limited and investors were accordingly cautious. After a weak start, the leading index SMI made up for initial losses over the course of the day and recently closed practically unchanged. In the forefront, stocks of various financial corporations received a boost. Swiss Re, UBS Swiss Life and Credit Suisse gained between 0.5% and 1.3%. ABB shares (+ 0.5%) continued last Friday's uptrend. The industrial group benefited from rumors that the mobility business, which, among other things, produces fast charging stations for electric vehicles, could be spun off and listed separately on the stock exchange at the beginning of 2022. Holcim was the weakest large-cap company with a minus of 0.6%. According to reports from the weekend press, the building materials company reached an agreement with US plaintiffs in connection with the use of expropriated land. The three heavyweights did not move from the spot. While Roche (+ 0.2%) was just able to stay in the positive zone, Novartis and Nestl (both -0.4%) lost ground without any significant news. In the broader market, Schindler shares were under above-average pressure with a price loss of 1.3%. The manufacturer of elevators and escalators was slowed down by mixed economic data from China. Almost two thirds of all newly installed elevators and escalators are built in the Middle Kingdom. The day's winners were the shares of Landis + Gyr, which jumped 8.7% after a major order from the USA. The order includes the installation of around 1.7 million electricity meters over the next 20 years as part of a modernization program for the network operator National Grid. The US stock markets were closed yesterday for Independence Day on Sunday. On the European stock markets, price movements were limited due to the lack of impetus from overseas and scant news from the corporate sector. The EuroStoxx50 gained 0.1%. From an industry perspective, financials were among the winners of the day, along with real estate companies and the basic materials sector. The latter benefited, among other things, from the recent rise in iron ore prices in mining stocks such as Rio Tinto (+ 1.4%) and BHP (+ 1.4%). On the other hand, so-called defensive sectors with little dependence on the economic environment had to accept levies, including the sectors of communication services, health and utilities. Capital markets returns 10 years: USA: 1,444%; DE: -0.210%; CH: -0.214% It almost seems as if the capital markets are already in the summer break. Since the recent assessments of the situation by the central banks, especially the US Federal Reserve, interest rates have been moving in a narrow sideways band. Nothing has changed in the last few days. Currencies US dollars in Swiss francs: 0.9209 euros in US dollars: 1.1878 euros in Swiss francs: 1.0939 The currency structure is stable overall, there was also a lack of impetus from both the market and the economy. The biggest shifts recently occurred in the euro and the US dollar. While the euro is primarily driven by the willingness to take risks, with the US dollar it is the expectation of further US monetary policy. The information in this document and in particular the description of individual securities do not constitute an offer to purchase the products or an invitation to another transaction. All information contained in this document has been carefully selected and comes from sources from the St.Galler Investment Center Kantonalbank AG can generally be regarded as reliable. Expressions of opinion or representations in this document can be changed at any time and without prior notice. No guarantee or responsibility is made regarding the accuracy and completeness of the information. St.Galler Kantonalbank AG is regulated and supervised by the Swiss Financial Market Supervisory Authority FINMA (Einsteinstrasse 2 3003 Bern Switzerland www.finma.ch). St. Galler Kantonalbank AG Investment Center | Matthias Mller CFA | Caroline Hilb \\ fRohstoffmrkte Oil price WTI: USD 76.68 per barrel Gold price: USD 1803.11 per ounce The oil price is still tending towards strength, but not only because of increasing demand in the wake of the economic recovery. A dispute between Saudi Arabia and the United Arab Emirates also gives him some tailwind. Because of a dispute between the two heavyweights, the members of OPEC and OPEC + could neither agree on a new subsidy scheme nor on a date for a next meeting. OPEC + would have liked to expand the production volume in order to meet the growing demand. But the dispute between the two heavyweights has put an end to this endeavor for the time being. Both parties are expected to announce their output for August later this week (possibly tomorrow). It is not clear who will benefit from the decision and how it will affect pricing. Some observers anticipate stronger price movements. We find that the devil doesn't have to be painted on the wall. But we will be watching the whole situation closely because rising commodity prices have fueled inflation concerns in recent weeks. It is not the first dispute between these two countries that has caused uncertainty. As early as December 2020, the two countries were unable to come to an agreement and the United Arab Emirates then threatened to leave. At that time the dispute ended with a kind of \"truce\". This time, however, the dispute went deeper, which is evident from the fact that the member states could not even agree on a new date. Economy No relevant economic data The information in this document, and in particular the description of individual securities, does not constitute an offer to purchase the products or a solicitation of any other transaction. All information contained in this document has been carefully selected and comes from sources from the Investment Center of St.Galler Kantonalbank AG can generally be viewed as reliable. Expressions of opinion or representations in this document can be changed at any time and without prior notice. No guarantee or responsibility is made regarding the accuracy and completeness of the information. St.Galler Kantonalbank AG is regulated and supervised by the Swiss Financial Market Supervisory Authority FINMA (Einsteinstrasse 2 3003 Bern Switzerland www.finma.ch). St. Galler Kantonalbank AG Investment Center | Matthias Mller CFA | Caroline Hilb \\ f",
