      teaser_text:
        "SIG Comibloc hat in den letzten Monaten an Kurswert zugelegt, den relevanten Branchenindex bertroffen sowie das von uns erwartete Zusatzpotenzial abgeschpft, weshalb wir die Aktie auf Neutral umstufen. ",
      body: "SIG Combibloc  Umstufung von Opportunitt auf Neutral SIG Combibloc  ISIN: CH0435377954 SIG Comibloc hat in den letzten Monaten an Kurswert zugelegt den relevanten Branchenindex bertroffen sowie das von uns erwartete Zusatzpotenzial abgeschpft weshalb wir die Aktie auf Neutral umstufen. Geschftsmodell SIG Combibloc mit Sitz in Neuhausen ist weltweit der zweitgrsste Hersteller von sterilen Verpackungslsungen fr die Nahrungsmittel und Getrnkeindustrie. SIG ist mit einemMarktanteil von rund 20% nach dem Marktfhrer Tetra Pak die Nr. 2 und der einzige globale Produzent. Das Unternehmen produziert die dazugehrigen Abfllmaschinen welche zur Herstellung von keimfreien Verpackungen verwendet werden selber. Diese Abfllmaschinen bentigen allesamt Verpackungslsungen die SIG den Kunden verkauft. Dieser hohe Anteil an wiederkehrenden Ertrgen ist die Hauptertragsquelle von SIG und ist als Rasierklingengeschftsmodell bekannt. Die wichtigsten Endabnehmer sind Unternehmen aus der relativ robustenGetrnke- und Nahrungsmittelindustrie. Die weltweit breit diversifizierte Kundenbasis mit langfristigen Kundenbeziehungen ist die Grundlage des Geschftsmodells. Neben den grossen multinationalen Unternehmen wie Nestl Danone und Unilever ist SIG auch stark bei kleineren und lokalen Kunden in den jeweiligen Regionen positioniert. Die Kundennhe ist auch ein wichtiger Faktor fr Innovationen. In Zusammenarbeit mit den Kunden entwickelt SIG laufend neue Abfllmaschinen Anwendungsmglichkeiten und Verpackungsmaterialien. DasThema Nachhaltigkeit gewinnt ebenfalls an Bedeutung. So hat SIG mit dem SIGNATURE PACK den ersten aseptischen Karton mit erneuerbaren Materialien auf Holzbasis entwickelt. Gegenwrtig sind die Kartonpackungen von SIG vollstndig recycelbar mit 70% bis 80% erneuerbarem Material. Ziel ist es diesen Anteil auf 100% zu erhhen. Grsster und wichtigster Aktionr ist mit etwas ber 50% der kanadische Private Equity Investor Onex welcher SIG an die Brse brachte. StrategieSIG setzt sich berdurchschnittliche Wachstumsraten zum Ziel. Zumal neben dem Marktfhrer Tetra Pak welcher rund 60% Marktanteil hat SIG die einzige globale Alternative ist. Das Marktwachstum im Bereich aseptische Kartonverpackungen in welchem SIG ttig ist wird in den kommenden Jahren auf 3.6% geschtzt. Mittels Neukundengewinnung strkerer Marktdurchdringung bestehender Kunden und Innovationen will SIG ein berdurchschnittliches Wachstum generieren. Wachstumspotenzial sieht das Management insbesondere in der weiterenErschliessung der Schwellenlnder wo die Nachfrage weiterhin berdurchschnittlich ist. So hat SIG gewisse Schwellenlnder in Asien oder in Sdamerika erst krzlich erschlossen und plant weitere Lnder direkt oder via Joint Venture zu erschliessen. Das Marktwachstum in den Schwellenlndern ist deutlich hher als in den gesttigten Mrkten. Der gesttigte europische Absatzmarkt stagniert und wchst nur minimal. In Nordamerika verfgt SIG wegen der geringen Marktpenetration hingegen nochber gengend Wachstumspotenzial. Ein zustzlicher Wachstumstreiber wre eine mgliche Substituierung von PET durch Papier. Fazit: Das integrierte Geschftsmodell von SIG gefllt uns. Die Gefahr von Nachahmerprodukten welche die wiederkehrenden Ertrge von SIG gefhrden sehen wir auch dank der hohen Innovationskraft als gering an. Die Abhngigkeit von der robusten Getrnke- und Nahrungsmittelindustrie zeigt sich in der soliden Unternehmensentwicklung von SIG. Wir rechnen in denkommenden Jahren mit einem anhaltenden positiven Wachstum fr aseptischen Karton und sehen weitere Marktanteilsgewinne fr SIG als realistisch an. Nach dem starken Kursanstieg hat die Aktie das von uns erwartete Zusatzpotenzial abgeschpft und ist in unserem Modell fair bewertet weshalb wir die Aktie auf Neutral umstufen. Einstufung: Neutral  Wir erachten SIG Combibloc als angemessen bewertet und sehen ein ausgewogenes Chancen-/Risikoprofil. ",
      body_in_english:
        "SIG Combibloc - upgraded from Opportunity to Neutral SIG Combibloc - ISIN: CH0435377954 SIG Comibloc has risen in price over the past few months, outperforming the relevant industry index and skimming off the additional potential we expected, which is why we are upgrading the share to Neutral. Business model SIG Combibloc, based in Neuhausen, is the world's second largest manufacturer of sterile packaging solutions for the food and beverage industry. With a market share of around 20%, SIG is number 2 after the market leader Tetra Pak and the only global producer. The company produces the associated filling machines which are used for the production of aseptic packaging itself. These filling machines all require packaging solutions that SIG sells to customers. This high proportion of recurring income is the main source of income for SIG and is known as the razor blade business model. The main end users are companies in the relatively robust beverage and food industries. The globally broadly diversified customer base with long-term customer relationships is the basis of the business model. In addition to the large multinational companies such as Nestl Danone and Unilever, SIG is also strongly positioned with smaller and local customers in the respective regions. Proximity to the customer is also an important factor for innovation. In cooperation with customers, SIG is constantly developing new filling machines, application options and packaging materials. The topic of sustainability is also gaining in importance. With the SIGNATURE PACK, SIG has developed the first aseptic carton with renewable wood-based materials. Currently, SIG's carton packs are fully recyclable with 70% to 80% renewable material. The aim is to increase this percentage to 100%. The largest and most important shareholder with a little over 50% is the Canadian private equity investor Onex, which brought SIG to the stock exchange. Strategy SIG aims to achieve above-average growth rates. In addition to the market leader Tetra Pak, which has a market share of around 60%, SIG is the only global alternative. The market growth in the area of aseptic carton packaging in which SIG is active is estimated at 3.6% in the coming years. SIG aims to generate above-average growth by gaining new customers, penetrating the market more strongly and innovations. The management sees growth potential in particular in the further development of the emerging markets, where demand continues to be above average. SIG has recently opened up certain emerging countries in Asia and South America and is planning to open up other countries directly or via joint ventures. The market growth in the emerging countries is significantly higher than in the saturated markets. The saturated European sales market is stagnating and growing only minimally. In North America, however, SIG still has sufficient growth potential due to the low market penetration. A possible substitution of PET by paper would be an additional growth driver. Conclusion: We like SIG's integrated business model. We consider the risk of copycat products that endanger the recurring income of SIG to be low, also thanks to the high level of innovation. The dependence on the robust food and beverage industry is reflected in the solid corporate development of SIG. We anticipate continued positive growth for aseptic cartonboard in the coming years and see further market share gains for SIG as realistic. After the strong price increase, the share has skimmed off the additional potential we expected and is fairly valued in our model, which is why we are upgrading the share to neutral. Classification: Neutral - We consider SIG Combibloc to be appropriately rated and see a balanced opportunity / risk profile.",
